@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(23deg, #16c957, #168dc9, #c80768);
    background-size: 600% 600%;

    -webkit-animation: AnimationName 4s ease infinite;
    animation: AnimationName 4s ease infinite;
}

@-webkit-keyframes AnimationName {
    0%{background-position:19% 0%}
    50%{background-position:82% 100%}
    100%{background-position:19% 0%}
}
@keyframes AnimationName {
    0%{background-position:19% 0%}
    50%{background-position:82% 100%}
    100%{background-position:19% 0%}
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-float infinite 3s ease-in-out;
            animation: App-logo-float infinite 3s ease-in-out;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: rgb(112, 76, 182);
}

@-webkit-keyframes App-logo-float {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes App-logo-float {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

.grid {
  display: flex;
  padding: 35px;
  flex-wrap: wrap;
  /* grid-template-columns:  1fr; */
  grid-gap: 20px;
  gap: 20px;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
/* .grid-item {
  display: grid;
  align-items: center;
  justify-items: center;
} */
.center {
  text-align: center;
}

.image img {
    width: 300px;
    height: 350px;
}

.image {
    display: flex;
    justify-content: center;
    flex-direction: column;
    border: 10px solid lightblue;
    background-color: lightblue;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 10px;
    border-radius: 20px;
    grid-gap: 20px;
    gap: 20px;
}
.image button {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 10px;
    border: 2px solid black;
    background-color: yellow;
    border-radius: 10px;
}
.joke-container {
    display: flex;
    background-color: lightgreen;
    border: lightgreen;
    width: 300px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    padding: 20px;
    font-family: 'Permanent Marker', cursive;
}



.urban {
    padding: 30px;
    border-radius: 10px;
    background-color: coral;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;

}

.urban h3 {
    background-color: lightyellow;
    padding: 5px;
    border-radius: 20px;
    text-align: center;
}
.urban p {
    background-color: lightblue;
    padding: 10px;
    border-radius: 20px;
}
.center {
    text-align: center;
}
.urban button {
    padding: 10px;
    border-radius: 10px;
    background-color: yellow;
    border: 2px solid black;
}
